@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

*{ box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body{
    font-family: 'Roboto', sans-serif;
}

section{
    width: 32rem;
    position: absolute;
    top: 50%;
    right: 50%;
    transform: translate(50%, -50%);

}
.personal{
    background-color: #231E39;
    padding-top: 5rem;
}
.personal .pro{
    background-color: #FEBB0B;
    color: #231E39;
    font-size: 1.4rem;
    position: absolute;
    top: 1.5rem;
    left: 2rem;
    padding: 0.1rem 0.7rem;
    border: 1px solid #FEBB0B;
    border-radius: 0.2rem;
}

.personal img{
    border-radius: 50%;
    border: 0.2rem solid #03BFCB;
    width: 12rem;
    height: 12rem;
    display: block;
    margin: 2rem auto 0 auto;
}
.personal .name{
    font-size: 5rem;
    text-align: center;
}
.personal .country{
    text-align: center;
    font-size: 1.3rem;
    margin: 0.2rem;
}
.personal .stack{
    text-align: center;
    font-size: 1.6rem;
    margin-top: 2.5rem;
}

.skills{
    background-color: #1F1A36;
}

li {
    list-style: none;
}